<template>
   <section>
      <div class="text-white">
         <div v-if="error_message" class="flex bg-red rounded-xl w-full text-left text-4xl p-4 my-3">
               <i class="fa fa-times-circle"></i>
               <h5 class="text-xl self-center px-2">
               Error enviando informacion. No se pudo crear el nuevo usuario
               <p>{{error_message_text}}</p>
               </h5>
         </div>
         <div v-if="success_message" class="flex bg-green-500 rounded-xl w-full text-left text-4xl p-4 my-3">
               <i class="fa fa-check-circle"></i>
               <h5 class="text-xl self-center px-2">La informacin ha sido enviada satisfactoriamente.</h5>
         </div>
      </div>               

      <crud-client :info="objId" :mode="modeAction" v-if="this.menuList['client']" v-on:actionInfo="messageInfo" />          
      <crud-roles :info="objId" :mode="modeAction" v-if="this.menuList['roles']" v-on:actionInfo="messageInfo" />          
      <crud-pole :info="objId" :mode="modeAction" v-if="this.menuList['pole']" v-on:actionInfo="messageInfo" />  
      <crud-pole-type :info="objId" :mode="modeAction" v-if="this.menuList['pole_type']" v-on:actionInfo="messageInfo" />  
      <crud-country :info="objId" :mode="modeAction" v-if="this.menuList['country']" v-on:actionInfo="messageInfo" />  
      <crud-currency :info="objId" :mode="modeAction" v-if="this.menuList['currency']" v-on:actionInfo="messageInfo" />  
      <crud-gender :info="objId" :mode="modeAction" v-if="this.menuList['gender']" v-on:actionInfo="messageInfo" />  
      <crud-product-type :info="objId" :mode="modeAction" v-if="this.menuList['product_type']" v-on:actionInfo="messageInfo" />  
      <crud-gallery :info="objId" :mode="modeAction" v-if="this.menuList['gallery']" v-on:actionInfo="messageInfo" />  
      <crud-gallery-filter :info="objId" :mode="modeAction" v-if="this.menuList['gallery_filter']" v-on:actionInfo="messageInfo" />  
      <crud-gallery-photo-filter :info="objId" :mode="modeAction" v-if="this.menuList['gallery_photo_filter']" v-on:actionInfo="messageInfo" />  
      <crud-gallery-i18n :info="objId" :mode="modeAction" v-if="this.menuList['gallery_i18n']" v-on:actionInfo="messageInfo" />  
      <crud-hotel :info="objId" :mode="modeAction" v-if="this.menuList['hotel']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-category :info="objId" :mode="modeAction" v-if="this.menuList['hotel_category']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-chain :info="objId" :mode="modeAction" v-if="this.menuList['hotel_chain']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-room :info="objId" :mode="modeAction" v-if="this.menuList['hotel_room']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-room-allotment :mode="modeAction" :info="objId" v-if="this.menuList['hotel_room_allotment']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-location :info="objId" :mode="modeAction" v-if="this.menuList['hotel_location']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-room-type :info="objId" :mode="modeAction" v-if="this.menuList['hotel_room_type']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-room-type-category :mode="modeAction" :info="objId" v-if="this.menuList['hotel_room_type_category']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-segment :info="objId" :mode="modeAction" v-if="this.menuList['hotel_segment']" v-on:actionInfo="messageInfo" />  
      <crud-hotel-segment-hotel :info="objId" :mode="modeAction" v-if="this.menuList['hotel_segment_hotel']" v-on:actionInfo="messageInfo" />  
      <crud-rate-offer :info="objId" :mode="modeAction" v-if="this.menuList['rate_offer']" v-on:actionInfo="messageInfo" />  
      <crud-rate-type :info="objId" :mode="modeAction" v-if="this.menuList['rate_type']" v-on:actionInfo="messageInfo" />  
      <!-- Prices -->
      <crud-hotel-room-price :info="objId" :mode="modeAction" v-if="this.menuList['hotel_price']" v-on:actionInfo="messageInfo" />  
   </section>
</template>
<script>
import CrudClient from "./common/CrudClient.vue";
import CrudRoles from "./common/CrudRoles.vue";

//Pole
import CrudPole from "./pole/CrudPole.vue";
import CrudPoleType from "./pole/CrudPoleType.vue";

//Common
import CrudCountry from "./common/CrudCountry.vue";
import CrudCurrency from "./common/CrudCurrency.vue";
import CrudGender from "./common/CrudGender.vue";
import CrudProductType from "./common/CrudProductType.vue";

//Gallery
import CrudGallery from "./gallery/CrudGallery.vue";
import CrudGalleryFilter from "./gallery/CrudGalleryFilter.vue";
import CrudGalleryPhotoFilter from "./gallery/CrudGalleryPhotoFilter.vue";
import CrudGalleryI18n from "./gallery/CrudGalleryI18n.vue";

//Hotel
import CrudHotel from "./hotel/CrudHotel.vue";
import CrudHotelCategory from "./hotel/CrudHotelCategory.vue";
import CrudHotelChain from "./hotel/CrudHotelChain.vue";
import CrudHotelRoom from "./hotel/CrudHotelRoom.vue";
import CrudHotelRoomAllotment from "./hotel/CrudHotelRoomAllotment.vue";
import CrudHotelLocation from "./hotel/CrudHotelLocation.vue";
import CrudHotelRoomPrice from "./hotel/CrudHotelRoomPrice.vue";
import CrudHotelRoomType from "./hotel/CrudHotelRoomType.vue";
import CrudHotelRoomTypeCategory from "./hotel/CrudHotelRoomTypeCategory.vue";
import CrudHotelSegment from "./hotel/CrudHotelSegment.vue";
import CrudHotelSegmentHotel from "./hotel/CrudHotelSegmentHotel.vue";

//Rate
import CrudRateOffer from "./Rate/CrudRateOffer.vue";
import CrudRateType from "./Rate/CrudRateType.vue";

export default {
   name: 'Dashboard',
   props: {
      menu: {
         default: 'client'
      },
      modeAction: {
         default: 'create'
      },
      objId: {
         default: 0
      },
   },
   components: {
      CrudClient,    
      CrudRoles,    
      CrudPole,
      CrudPoleType,
      CrudCountry,
      CrudCurrency,
      CrudGender,
      CrudProductType,
      CrudGallery,
      CrudGalleryFilter,
      CrudGalleryPhotoFilter,
      CrudGalleryI18n,
      CrudHotel,
      CrudHotelCategory,
      CrudHotelChain,
      CrudHotelRoom,
      CrudHotelRoomAllotment,
      CrudHotelLocation,
      CrudHotelRoomPrice,
      CrudHotelRoomType,
      CrudHotelRoomTypeCategory,
      CrudHotelSegment,
      CrudHotelSegmentHotel,
      CrudRateOffer,
      CrudRateType,
   },
   data() {
      return {
         error_message_text: '',
         error_message: false,
         success_message: false,
         menuList: {
            client: true,        
            roles: false,
            pole: false,
            pole_type: false,        
            country: false,
            currency: false,
            gender: false,
            product_type: false,
            gallery: false,
            gallery_photo_filter: false,
            gallery_filter: false,
            gallery_i18n: false,
            hotel: false,        
            hotel_category: false,
            hotel_chain: false,
            hotel_room: false,
            hotel_room_allotment: false,
            hotel_location: false,
            hotel_room_type: false,
            hotel_room_type_category: false,
            hotel_segment: false,
            hotel_segment_hotel: false,        
            rate_offer: false,        
            rate_type: false,        
            hotel_price: false,
         },
      }
   },
   created() {
      this.updActiveMenu(this.menu)
   },
   computed: {   
   },  
   methods: {     
      updActiveMenu(view) {
         const keys  = Object.keys(this.menuList)               
         keys.map(item => {
            this.menuList[item] = false
         })
         this.menuList[view] = true         
      },
      messageInfo(info){
         if(info.type == 'error') {
         this.error_message_text = info.text
         this.error_message = true
         this.success_message = false
         } else {
         this.error_message = false
         this.success_message = true
         }
         setTimeout(() => {
         this.error_message = false
         this.success_message = false
         }, 3000);
      }, 
   }
}
</script>
