import Authorization from "../user/auth.service";

const GalleryService = {
    getList() {
        try {
            return Authorization.checkLogin(`/images`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            class_name: item.class_name,
                            id_object_class: item.id_object_class,
                            subcategory: item.subcategory,
                            /* lang: item.lang,
                            image_keyword: item.image_keyword,
                            ext: item.ext,
                            anno: item.anno,
                            order: item.order, */
                            active: (item.active) ? true : false,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    /* getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.alias
                })
            }
            return result
        })

    }, */
    async getGallery(id,class_name) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/image/${class_name}/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdGallery(gallery_data, is_new = true) {
        try {
            gallery_data.auth = 'bearer'
            console.log(gallery_data)
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/images/create', gallery_data, 'post'):
                await Authorization.checkLogin(`/images/upd/${gallery_data.id}`, gallery_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
}

export default GalleryService