<template>
  <header class="flex it-header-menu relative border-b shadow bg-white py-4 px-6">
    <figure>
      <router-link :to="'/'">
        <!-- <img src="https://archive.org/download/logo-blue/logo-blue.png" class="w-3/5 m-auto" alt="logo" /> -->
        <img src="@/assets/img/logo-blue.png" class="w-2/5 pl-10" alt="logo" />
      </router-link>
    </figure>  
    <div class="flex justify-end align-center gap-x-4 m-auto w-90x100 py-3">
        <span class="self-center">
          <i class="fa fa-user"></i>
          {{ user_name }}          
        </span>
        <button class="text-2xl" @click="logout"><i class="fas fa-sign-out-alt"></i></button>
    </div>            
  </header>
</template>
<script>
import authService from "@/services/user/auth.service";

export default {  
  name: "Header",  
  data() {
    return {
      openMenu: false,
      showNavMobile: false,
      isLogged: false,
      user_name: ''
    };
  },
  created() {
    const token = authService.getClientToken()
    this.user_name = token.client.name
  },  
  methods: {
    async logout() {
      try {
          // Llamando al API para desloguearse //     
          const {status, data} = await authService.checkLogin(`/clients/logout`, {auth: 'bearer'})
          if(status == 200) {
              sessionStorage.setItem('token', data.token)              
              authService.destroyLocalUserData()
              if (this.$route.fullPath != 'Login') {
                this.$router.push({name: 'Login'})
              }
          } else {
              console.log('problemas al desloguearse')
          }
      } catch(e) {                
          console.log(e)
      }
    }
  },
};
</script>
